import { Component, Inject, OnInit } from "@angular/core";
import { FormGroup } from "@angular/forms";
import { MAT_DIALOG_DATA, MatDialog, MatDialogRef } from "@angular/material/dialog";
import { IAlertConfig } from "@shared/models/alert";
import { AppAlertService } from "@shared/service/app/alert.service";
import { EmpresaService } from "@shared/service/app/empresa.service";
import { finalize, firstValueFrom, Observable,Subject, takeUntil, takeWhile, tap, timer } from "rxjs";
import { MY_DATE_FORMATS } from "@shared/constants/date-format";
import { MAT_DATE_FORMATS } from "@angular/material/core";
import { ArticulosService } from "../../service/articulos.service";
import { MatTableDataSource } from "@angular/material/table";
import { IItemOrdenCompraPendiente } from "app/modules/compras/ordenes-de-compra/models/orden-de-compra";
import { IArticulo } from "../../models/articulo";
import { environment } from "environments/environment";
import { SaveComponent } from "app/modules/compras/ordenes-de-compra/components/save/save.component";

@Component({
    selector: 'app-entrega-pendiente',
    templateUrl: './entrega-pendiente.component.html',
    styleUrls: ['./entrega-pendiente.component.scss'],
    providers: [{ provide: MAT_DATE_FORMATS, useValue: MY_DATE_FORMATS }]
})
export class EntregaPendienteComponent implements OnInit {
    estados: any[] = [];
    filteredEstado: Observable<any[]>;
    form: FormGroup;
    articulo: IArticulo
    items_oc_pendientes: IItemOrdenCompraPendiente[];
    showAlert = false;
    _alertConfig: IAlertConfig;
    color_primario: string;
    color_secundario: string;
    default_color: string = 'bg-primary';

    headers = ['N° OC', 'Proveedor', 'Solicitado', 'Cantidad Pendiente', 'Remitos Realizados', 'Acciones'];
    column_params = ['fecha_numero_oc', 'razon_social', 'cantidad_pedida', 'cantidad_pendiente', 'cantidad_remitos_oc', 'acciones'];
    funcionesObjeto = null;
    env = null;
    mostrar_tabla: boolean;
    puede_crear: boolean;
    public dataSource: MatTableDataSource<any> = new MatTableDataSource();
    private _unsubscribeAll: Subject<any> = new Subject<any>();

    constructor(
        @Inject(MAT_DIALOG_DATA) public data: any,
        private _empresaService: EmpresaService,
        private _articulosService: ArticulosService,
        public alert: AppAlertService,
        private dialogo: MatDialog,
        public dialogRef: MatDialogRef<IItemOrdenCompraPendiente>
    ) {
        this.articulo = this.data ? this.data : null;

        /**
     * Aca se declaran los botones que iran en la grilla sobre el apartado de acciones.
     */
        this.funcionesObjeto = [
            {
                icono: 'view',
                nombre_boton: "Ver Orden de Compra",
                functionName: 'verOrdenCompra',
                iconoAccion: (item: IItemOrdenCompraPendiente) => 'jedstion:ver',
                iconoAccionado: (item: IItemOrdenCompraPendiente) => '',
                iconoDeshabilitado: (item: IItemOrdenCompraPendiente) => '',
            }
        ];

        this.env = environment.stock.view_items_oc_pendientes + '?' + `filter[idarticulo]=${this.articulo.idarticulo}&filter[pendiente_entrega]=1&sort=-idordencompra`;
    }

    ngOnInit(): void {
        // Subscribe to empresa data
        this._empresaService.empresa$
            .pipe(takeUntil(this._unsubscribeAll))
            .subscribe(empresa => {
                this.color_primario = empresa.color_primario;
                this.color_secundario = empresa.color_secundario;
            });

        // Load empresa data
        this._empresaService.getEmpresa();
        this.subscribeAlert();
    }

    // Alert
    subscribeAlert() {
        this.alert.emitConfig.subscribe({
            next: (config: IAlertConfig) => {
                this.showAlert = true;
                this._alertConfig = {
                    ...config,
                    appearance: 'border',
                };
                let time = 2;
                timer(100, 1500)
                    .pipe(
                        finalize(() => {
                            this.showAlert = false;
                            this._alertConfig = null;
                        }),
                        takeWhile(() => time > 0),
                        takeUntil(this._unsubscribeAll),
                        tap(() => (time -= 1))
                    )
                    .subscribe();
            },
        });
    }

    onCloseMenu() {
        this.dialogRef.close(false)
    }

    /**
    * Esta es la funcion que se encarga de la columna acciones dentro de la grilla dinamica
    * @param event 
    */
    generarAcciones(event: { name: string, params: any }) {
        switch (event.name) {
            case 'verOrdenCompra':
                this.verOrdenCompra(event);
                break;
        }
    }

    /**
     * Esta funcion ve la informacion de un item
     * @param event 
    */
    async verOrdenCompra(event) {
        let ordenCompra = await firstValueFrom(this._articulosService.getOrdenesDeCompra(event.params.data.idordencompra));

        ordenCompra = ordenCompra['list'].map((item) => {
            const fecha = item.fecha;
            const [anio, mes, dia] = fecha.split("-");
            const fechaFormateada = `${dia}/${mes}/${anio}`;

            return {
                ...item,
                fecha: fechaFormateada,
            }
        })

        const modal_config = {
            model: ordenCompra[0],
            title: `Ver Orden de compra ${event.params.data.fecha_numero_oc}`,
            soloLectura: true
        };

        this.dialogo
            .open(SaveComponent, { data: modal_config, panelClass: 'my-dialog', autoFocus: false })
            .componentInstance.editadoExitosamente
            .subscribe({
                next: () => { },
            });
    }

}